#main_wrapper {
  width: 100%;
  height: 100%;
  position: fixed;
  transition: all 1s;
  z-index: 10;
}
.background_video_wrapper {
  width: 100vw;
  height: 100vh;
  position: fixed;
}
.background_video {
  width: 100%;
  height: 100%;
  background-size: contain;
  position: inherit;
  object-fit: fill;
}
.intro_wrapper {
  width: 100vw;
  height: 100vh;
  position: fixed;
  z-index: 20;
  background: #1d2b4a;
  transition: opacity 2s;
}
.intro_wrapper_clicked {
  width: 100vw;
  height: 100vh;
  position: fixed;
  z-index: 20;
  background: #1d2b4a;
  transition: opacity 2s;
  opacity: 0;
  pointer-events: none;
}
.intro_video {
  width: 100%;
  height: 100%;
  position: inherit;
  cursor: pointer;
}

.phone_container {
  position: absolute;
  width: 100vw;
  height: 100vh;
  z-index: 10;
  display: flex;
  justify-content: center;
  align-items: center;
}
.phone {
  width: 800px;
  height: 400px;
  position: absolute;
  margin: auto;
  transition: transform 2s;
}

.screen {
  visibility: visible;
  position: absolute;
  width: 762px;
  height: 355px;
  top: 23px;
  left: 19.5px;
  background: #000;
  opacity: 1;
  z-index: 10;
  transition: opacity 2s, height 0.5s;
}
.phone_video {
  position: absolute;
  width: 761px;
  height: 355px;
  top: 23px;
  left: 19.5px;
  z-index: 10;
  opacity: 0;
  object-fit: fill;
  transition: all 0.5s;
  cursor: pointer;
}
.freepik {
  display: none;
}

#info {
  width: 100vw;
  height: 100vh;
  position: fixed;
  background: #000;
  z-index: 0;
}
.info_wrapper {
  width: 100vw;
  height: 100vh;
  position: fixed;
  z-index: 20;
  background: #1d2b4a;
  transition: opacity 2s;
}

.info_video {
  width: 100%;
  height: 100%;
  position: inherit;
  cursor: pointer;
}

@media all and (max-width: 1024px) {
  #main_wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    background: #000;
  }
  .background_video {
    height: auto !important;
  }
}
